html {
	height: 100%;
	text-rendering: optimizeLegibility;
	-moz-osx-font-smoothing: grayscale;
	font-smoothing: antialiased;
	-webkit-font-smoothing: antialiased;
}
body {
	font-family: 'Montserrat', sans-serif;
    margin: 0;
    background-repeat: no-repeat;
    background-attachment: fixed;
    background: linear-gradient(#515D97, #404750);
}

//Vaak gebruikte kleuren
$dark-blue: #343A55;
$light-blue: #485170;

// Responsive breakpoints
// Gebruik: @include bp(W...) {
//   hier de stijl die alleen geldt voor die screen size
// }
@mixin bp($point) {
	@if $point == W1250 {
		@media (max-width: 1250px) { @content; }
	}
	@else if $point == W1100 {
		@media (max-width: 1100px) { @content; }
	}
	@else if $point == W768 {
		@media (min-width: 768px) { @content; }
	}
	@else if $point == M768 {
		@media (min-width: 768px) { @content; }
	}
}

#mt-big {
	margin-top: 40px;
}
#mt-medium {
	margin-top: 30px;
}
#mt-small {
	margin-top: 20px;
}
#mt-xs {
	margin-top: 10px;
}

#header {
	margin-top: 40px;
	text-align: center;
	#logo {
		width: 70%;
		@include bp(M768) {
			width: 600px;
		}
	}
	#subtext {
		color: white;
		text-align:center;
		font-weight: 400;
		font-size: 14px;
		margin-top: 5px;
	}
}

h3 {
	padding-top: 10px; 
	color: white;
	text-align:center;
	font-size:18px;
	font-weight: 500;

}

#flags {
	text-align: center;
	img {
		width:100px;
		margin: 20px;
		border-radius:20px;
	}
} 

