#inputfields {
	text-align:center;
	input, select {
		font-family: 'Montserrat', sans-serif;
		text-align-last: center;
		width: 70%;
		text-align: center;
		border-radius: 10px;
		-moz-border-radius: 10px;
		-webkit-border-radius: 10px;
		background-color: $dark-blue;
		border: none;
		font-size: 16px;
		color: $light-blue;
		margin-bottom: 15px;
		padding: 19px 0;
		font-weight: 500;
		&::placeholder {
			color: $light-blue;
		}
		&:focus {
			outline: none;
		}
	}
}

#inputfields button{
	font-family: 'Montserrat', sans-serif;
	background-color: #fff;
	color: $light-blue;
	border: none;
	width:70%;
	text-align:center;
    padding: 20px;
    margin-top: 20px;
    font-size: 19px;
    font-weight: 500;
	border-radius: 80px;
	&:focus {
		outline: none;
	}
	&:hover {
		background-color: $dark-blue;
		color: #fff;
	}
}

#questionform img {
	margin-right:3%;
	margin-bottom:2%;
	width:7%;
	-webkit-clip-path: circle(50% at 50% 50%);
	clip-path: circle(50% at 50% 50%);
}

#questionform {
	text-align:center;
}

#nextquestion {
	margin-top: 50px !important;
    font-weight: 500 !important;
    font-size: 15px;
	width:70%;
	text-align:center;
	padding-bottom:10px;
	padding-top:10px;
	margin:auto;
	display:block;
	border-radius:15px;
	@include bp(M768) {
		width: 600px;
	}
}